body {
  background-color: #5b6467;
  background-image: linear-gradient(315deg, #5b6467, #8b939a 74%);
  background-image: linear-gradient(147deg, #000, #2c3e50 74%);
  background-repeat: no-repeat;
  height: 100vh;
  margin: 0;
  overflow: hidden;
  padding: 0;
  position: relative;
  width: 100vw;
}
section.counter-app {
  align-items: center;
  color: #fff;
  display: flex;
  flex-direction: column;
  font-family: Ubuntu;
  justify-content: center;
  left: 50%;
  min-width: 80vw;
  position: fixed;
  top: 30%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
section.counter-app h1 {
  text-transform: uppercase;
}
section.counter-app h2 {
  color: gold;
}
section.counter-app div.buttons {
  margin-top: 16px;
}
section.counter-app div.buttons button {
  background-color: transparent;
  border: 2px solid #fff;
  border-radius: 25px;
  color: #fff;
  cursor: pointer;
  margin: 0 2px;
  padding: 4px 8px;
  text-transform: uppercase;
  transition: 0.3s ease-in;
}
section.counter-app div.buttons button:hover {
  border-color: gold;
  color: gold;
}
